"use client"

import { useState } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Slider } from "@/components/ui/slider"
import { Card, CardContent } from "@/components/ui/card"
import { ChevronLeft, ChevronRight, Gamepad2, Trophy, Users } from 'lucide-react'

export default function SignUpForm() {
  const [step, setStep] = useState(1)
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    confirmPassword: "",
    gamingExperience: 5,
    preferredPlatform: "",
    favoriteGenre: "",
    playingHours: [2, 6], // Hours per week
    teamPreference: "",
  })

  const updateFormData = (field: string, value: string | number | number[]) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const platforms = [
    "PC",
    "PlayStation",
    "Xbox",
    "Nintendo Switch",
    "Mobile",
    "Multiple"
  ]

  const genres = [
    "Action",
    "Adventure",
    "RPG",
    "FPS",
    "Strategy",
    "Sports",
    "MOBA",
    "Battle Royale"
  ]

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    // Add your form submission logic here
    console.log("Form submitted:", formData)
  }

  const steps = [
    {
      title: "Account Details",
      icon: <Users className="w-6 h-6" />,
      content: (
        <div className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="username">Username</Label>
            <Input
              id="username"
              placeholder="Enter your gaming username"
              value={formData.username}
              onChange={(e) => updateFormData("username", e.target.value)}
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="email">Email</Label>
            <Input
              id="email"
              type="email"
              placeholder="Enter your email"
              value={formData.email}
              onChange={(e) => updateFormData("email", e.target.value)}
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="password">Password</Label>
            <Input
              id="password"
              type="password"
              placeholder="Create a password"
              value={formData.password}
              onChange={(e) => updateFormData("password", e.target.value)}
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="confirmPassword">Confirm Password</Label>
            <Input
              id="confirmPassword"
              type="password"
              placeholder="Confirm your password"
              value={formData.confirmPassword}
              onChange={(e) => updateFormData("confirmPassword", e.target.value)}
            />
          </div>
        </div>
      ),
    },
    {
      title: "Gaming Experience",
      icon: <Trophy className="w-6 h-6" />,
      content: (
        <div className="space-y-6">
          <div className="space-y-4">
            <Label>Years of Gaming Experience</Label>
            <div className="space-y-2">
              <Slider
                defaultValue={[formData.gamingExperience]}
                max={20}
                step={1}
                onValueChange={([value]) => updateFormData("gamingExperience", value)}
              />
              <div className="text-center text-sm text-muted-foreground">
                {formData.gamingExperience} years
              </div>
            </div>
          </div>
          <div className="space-y-2">
            <Label>Preferred Platform</Label>
            <Select
              value={formData.preferredPlatform}
              onValueChange={(value) => updateFormData("preferredPlatform", value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select your main gaming platform" />
              </SelectTrigger>
              <SelectContent>
                {platforms.map((platform) => (
                  <SelectItem key={platform} value={platform.toLowerCase()}>
                    {platform}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
      ),
    },
    {
      title: "Gaming Preferences",
      icon: <Gamepad2 className="w-6 h-6" />,
      content: (
        <div className="space-y-6">
          <div className="space-y-2">
            <Label>Favorite Genre</Label>
            <Select
              value={formData.favoriteGenre}
              onValueChange={(value) => updateFormData("favoriteGenre", value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select your favorite genre" />
              </SelectTrigger>
              <SelectContent>
                {genres.map((genre) => (
                  <SelectItem key={genre} value={genre.toLowerCase()}>
                    {genre}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="space-y-4">
            <Label>Playing Hours (per week)</Label>
            <div className="space-y-2">
              <Slider
                defaultValue={formData.playingHours}
                min={0}
                max={40}
                step={1}
                onValueChange={(value) => updateFormData("playingHours", value)}
              />
              <div className="text-center text-sm text-muted-foreground">
                {formData.playingHours[0]} - {formData.playingHours[1]} hours per week
              </div>
            </div>
          </div>
          <div className="space-y-2">
            <Label>Team Preference</Label>
            <Select
              value={formData.teamPreference}
              onValueChange={(value) => updateFormData("teamPreference", value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select your team preference" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="solo">Solo Player</SelectItem>
                <SelectItem value="casual-team">Casual Team Player</SelectItem>
                <SelectItem value="competitive-team">Competitive Team Player</SelectItem>
                <SelectItem value="flexible">Flexible</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </div>
      ),
    },
  ]

  return (
    <div className="min-h-screen bg-gradient-to-br from-indigo-900 via-purple-900 to-pink-900 py-12 px-4 sm:px-6 lg:px-8">
      <Card className="max-w-2xl mx-auto">
        <CardContent className="p-6">
          <div className="space-y-6">
            {/* Progress Steps */}
            <div className="flex justify-between items-center mb-8">
              {steps.map((s, i) => (
                <div key={i} className="flex flex-col items-center">
                  <div
                    className={`w-10 h-10 rounded-full flex items-center justify-center ${
                      step > i
                        ? "bg-green-500"
                        : step === i + 1
                        ? "bg-blue-500"
                        : "bg-gray-300"
                    } text-white`}
                  >
                    {s.icon}
                  </div>
                  <span className="text-sm mt-2">{s.title}</span>
                </div>
              ))}
            </div>

            {/* Form Content */}
            <form onSubmit={handleSubmit}>
              <AnimatePresence mode="wait">
                <motion.div
                  key={step}
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  transition={{ duration: 0.3 }}
                >
                  {steps[step - 1].content}
                </motion.div>
              </AnimatePresence>

              {/* Navigation Buttons */}
              <div className="flex justify-between mt-8">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => setStep(step - 1)}
                  disabled={step === 1}
                >
                  <ChevronLeft className="w-4 h-4 mr-2" />
                  Previous
                </Button>
                <Button
                  type={step === steps.length ? "submit" : "button"}
                  onClick={() => step < steps.length && setStep(step + 1)}
                >
                  {step === steps.length ? (
                    "Complete Registration"
                  ) : (
                    <>
                      Next
                      <ChevronRight className="w-4 h-4 ml-2" />
                    </>
                  )}
                </Button>
              </div>
            </form>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

